<script setup>
import { defineProps } from 'vue';
import MdiDelete from '../Icons/MdiDelete.vue'
import Swal from 'sweetalert2'
import http from '../../services/api/axios';
import EditPen from "@/components/Icons/EditPen.vue";
import router from "@/router";
defineProps({
  tableBody: {
    type: Array,
    required: true,
  },
  viewFor: {
    type: String,
  },
});
function deleteWorker(data) {
  Swal.fire({
      title: "Haqiqatdan ham o'chirishni istaysizmi?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#DD6C6C",
      cancelButtonColor: "#06B68C",
      cancelButtonText: "Bekor qilish",
      confirmButtonText: "Ha o'chiraman",
    }).then(async (result) => {
      if (result.isConfirmed) {
        http.delete('/auth/users/'+data.id + '/').then(()=> {
          window.location.reload()
        })
      }
    });
}
function editWorker (data) {
  console.log(data)
  router.push({
    name: 'EditWorker',
    params: {
      id: data.id
    }
  })
}
</script>

